@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;600;700;800&display=swap");

:root {
  --overlay-color: #03a9f4;
  --translucent-background: rgb(29, 144, 208, 0.85);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Oswald", sans-serif;
}

.showcase {
  position: absolute;
  right: 0;
  width: 100%;
  min-height: 100vh;
  padding: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #111;
  color: white;
  z-index: 2;
  transition: 0.5s;
}

.showcase.active {
  right: 300px;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 100px;
  z-index: 1000;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.toggle {
  position: relative;
  cursor: pointer;
  font-size: 2rem;
  text-align: center;
}

.toggle.active {
  position: relative;
  cursor: pointer;
  font-size: 2rem;
  text-align: center;
}

.video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.9;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--overlay-color);
  mix-blend-mode: overlay;
}

/* Weather Card */

.weather {
  margin-left: 2rem;
  z-index: 5;
  display: flex;
  justify-content: center;
}

.weather-card {
  width: 70%;
}

.search-forecast-div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}

.card-title {
  font-weight: 600;
  font-size: 2.2rem;
  margin-bottom: 20px;
  border-bottom: 4px solid #ffffffb5;
  margin-left: auto;
  margin-right: auto;
  color: black;
  background: var(--translucent-background);
  padding: 20px;
  border-radius: 20px;
}

.card-text {
  font-weight: 600;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.card img {
  height: 150px;
}

/* Search Box*/

.search-container {
  height: 300px;
  width: 400px;
  margin-right: 2rem;
  margin-top: 10rem;
  background: var(--translucent-background);
  z-index: 5;
  padding: 20px;
  border-radius: 20px;
}

.text {
  color: #000000;
}

.text h2 {
  font-size: 1.7em;
  font-weight: 800;
  line-height: 1.5em;
  max-width: 600px;
  margin-left: 20px;
}

.text h3 {
  font-size: 1em;
  font-weight: 700;
  line-height: 1em;
  margin: 20px 0px;
  max-width: 400px;
  border-bottom: 4px solid transparent;
}

.greeting-h2-div {
  margin-bottom: 20px;
}

.form-div {
}

.social-submit {
  display: flex;
  flex-direction: row;
  height: 50px;
  width: 100%;
  margin-top: 1rem;
  justify-content: center;
}

.form-label {
  margin-right: 1rem;
  display: inline-block;
  width: 50px;
}

.city-input {
  margin-bottom: 0.5rem;
}

.form-control {
  margin-right: 1rem;
  width: 100px;
  border: 3px solid black;
  border-radius: 5px;
  height: 1.5rem;
  text-align: center;
}

.learn-more:hover {
  letter-spacing: 2.5px;
  padding: 12px;
}

.social {
  display: flex;
  justify-content: center;
}

.social li {
  list-style: none;
}

.social li a {
  margin-right: 10px;
  font-size: 1.8rem;
  transition: 0.5s;
}

.social li a:hover {
  font-size: 2rem;
  cursor: pointer;
}

.submit-button {
  font-size: 1.2em;
  font-weight: 700;
  background: #3197f7;
  border: 3px solid black;
  color: black;
  border-radius: 5px;
  margin-left: 30px;
  padding: 5px;
  text-decoration: none;
  letter-spacing: 1px;
  transition: 0.3s;
}

.menu {
  position: absolute;
  top: 0;
  right: 0;
  width: 300px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.menu ul {
  position: relative;
  list-style: none;
}

.menu ul li {
  margin-bottom: 20px;
}

.menu ul li a {
  text-decoration: none;
  font-size: 24px;
  color: black;
}

.menu ul li a:hover {
  color: var(--overlay-color);
}

@media (max-width: 875px) {
  .showcase,
  .showcase header {
    padding: 40px;
  }

  .text h2 {
    font-size: 1.2em;
  }
}

@media (max-width: 1225px) {
  .search-container,
  .weather {
    margin-right: auto;
    margin-left: auto;
  }
}

@media (max-width: 1100px) {
  .search-container {
    margin-top: 5rem;
  }

  .search-container {
    max-height: 250px;
    width: 85%;
  }

  .card img {
    height: 80px;
  }

  .card-title {
    font-size: 1.2rem;
  }
}

/* Phones */
@media (max-width: 700px) {
  .logo {
    font-size: 1.5rem;
  }
  .header {
    padding: 20px 20px;
  }
}
